.loading-animation {
  display: flex;
  flex-direction: row;
  opacity: 1;
  animation: fadeIn 0.2s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fading-out {
  display: flex;
  flex-direction: row;
  opacity: 0;
  animation: fadeOut 0.2s;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
